import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';
import './Detail.scss';

let Box = styled.div`
  padding-top : 30px;
  display : block;
`;

let Title = styled.h4`
  font-size : 25px;
  color : ${probs => probs.color}
`;


function Detail(probs) {
    let [alert, setAlert] = useState(true);
    let [input, setInput] = useState('');

    // when component mount & update
    useEffect(() => {
        let timer = setTimeout(() => { setAlert(false) }, 2000);
        // when unmount, remove timer 
        return () => { clearTimeout(timer) }
    }, [alert]); // condition when changed alert 
    // first time when unmount, changed state
    // }, []); 

    // ../detail/:id/:name  
    let { id } = useParams();
    // let curId = probs.shoes.findIndex(obj => obj.id == id);
    // {probs.shoes[curId].title}
    // let curShoes = probs.shoes.find(x => x.id == id);
    let curShoes = probs.shoes.find(function (product) {
        return product.id == id
    });

    let history = useHistory();

    return (
        <>
            <div className="container">
                <Box>
                    <Title className="red">Detail</Title>
                    {/* <Title color="blue">Detail</Title>
                    <Title color={'red'}>Hi</Title> */}
                </Box>

                {/* {input}
                <input onChange={(e) => { setInput(e.target.value) }} /> */}

                {
                    alert === true
                        ? (<div className="my-alert-red">
                            <p>재고가 얼마 남지 않았습니다!</p>
                        </div>)
                        : null
                }

                <div className="row">
                    <div className="col-md-6">
                        <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                    </div>
                    <div className="col-md-6 mt-4">
                        <h4 className="pt-5">{curShoes.title}</h4>
                        <p>{curShoes.content}</p>
                        <p>{curShoes.price}</p>

                        <StateStock stock={probs.stock[id]}></StateStock>
                        <button className="btn btn-danger" onClick={() => {
                            let copyStock = [...probs.stock];
                            copyStock[id]--;
                            probs.setState(copyStock);
                        }}>주문하기</button>
                        &nbsp;
                        <button className="btn btn-danger" onClick={() => {
                            history.goBack();
                            // 특정 경로로 이동
                            // history.push('/');
                        }}>뒤로가기</button>
                    </div>
                </div>
            </div>
        </>
    )
}

function StateStock(probs) {
    return (
        <p>STOCK : {probs.stock}</p>
    )
}

export default Detail;