/* eslint-disable */
import { useState } from 'react';
import { Navbar, Container, Nav } from 'react-bootstrap';
import { useHistory } from 'react-router-dom';
import './App.css';
import defaultShoes from './data.js';
import Detail from './Detail.js';
import useLocalStorage from './useLocalStorage.js';
import axios from 'axios';

import { Link, Route, Switch } from 'react-router-dom';

function App() {
  let [shoes, setShoes] = useState(defaultShoes);
  let [stock, setStock] = useState([10, 11, 12]);

  return (
    <>
      <div className="App">
        <Navbar bg="dark" variant="dark">
          <Container>
            <Navbar.Brand href="#home">ABC-MART</Navbar.Brand>
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">Home</Nav.Link>
              <Nav.Link as={Link} to="/detail">Detail</Nav.Link>
              <Nav.Link href="#pricing">WOMEN</Nav.Link>
            </Nav>
          </Container>
        </Navbar>
      </div>

      <Switch> //route 하나만 보여주셈
        <Route exact path="/">
          <Main shoes={shoes} func={setShoes}></Main>
        </Route>

        {/* :id => 뒤에 아무 문자가 오든 간에 */}
        <Route path="/detail/:id">
          <Detail shoes={shoes} stock={stock} setState={setStock}></Detail>
        </Route>
        {/* <Route path="/어쩌고" component={Modal}></Route> */}
      </Switch>

    </>
  );
}

function Card(probs) {
  return (
    <div className="col-md-4">
      <img src={'https://codingapple1.github.io/shop/shoes' + probs.idx + '.jpg'} width="100%" />
      <h4>{probs.shoes.title}</h4>
      <p>{probs.shoes.content} {probs.shoes.price}</p>
    </div>
  )
}

function Main(probs) {
  //  let [more, setMore] = useLocalStorage("more", 0);
  let [more, setMore] = useState(1);

  return (
    <>
      <div className="background">
        <h1>Up to 20% OFF!!</h1>
        <p>Anniversary Sale</p>
        <p>
          <button variant="primary">Learn more</button>
        </p>
      </div>

      <div className="container">
        <div className="row">
          {
            probs.shoes.map(function (obj, i) {
              return (
                <Card shoes={obj} idx={i + 1} key={i}></Card>
              )
            })
          }
        </div>
        <button className="btn btn-primary" onClick={() => {

          // axios.post('server URL', {id: 'abcd', pw: 1234}).then()

          setMore(more + 1);
          let data = 'https://codingapple1.github.io/shop/data' + (more + 1) + '.json';
          // occured error

          // get data from the server
          axios.get(data)
            .then((result) => {
              // console.log(result)
              // Axios automatically changes json to object.
              console.log(result.data);

              // 더보기 상품 3개 추가하기
              probs.func([...probs.shoes, ...result.data]);
              console.log(probs.shoes);
              // let copyShoes = [...probs.shoes, ...result.data];
              // probs.func(copyShoes);
            })
            .catch(() => {
              console.log('failed')
            })

        }}>더보기</button>
      </div>
    </>
  )
}

export default App;
